<template>
    <div class="card-scene">
        <el-tabs v-model="activeName" @tab-click="resetPlay">
            <el-tab-pane label="Rules" name="rules">
                <rules v-on:start="makeYourGroup"></rules>
            </el-tab-pane>
            <el-tab-pane label="Make your group" name="config">
                <el-row v-for="(family,index) in setupScene" :key="family.id" gutter="20" style="margin-bottom: 20px;">
                    <el-col :span="1">
                        <v-swatches v-model="family.color" popover-x="left"
                                    v-on:close="changeFamilyColor(family.id)"></v-swatches>
                    </el-col>
                    <el-col :span="6">
                        <el-input placeholder="family name" v-model="family.name" clearable v-on:change="saveGroup()"></el-input>
                    </el-col>
                    <el-col :span="1">
                        <el-button v-on:click="removeFamily(index)" type="text" class="remove-button">
                            <i class="fas fa-minus-circle"></i>
                        </el-button>
                    </el-col>
                    <el-col :span="12">
                        <el-row v-for="(men,indexMen) in family.people" :key="indexMen" gutter="20"
                                style="margin-bottom: 10px;">
                            <el-col :span="12">
                                <el-input placeholder="name" v-model="men.name" clearable v-on:change="saveGroup()"></el-input>
                            </el-col>
                            <el-col :span="6">
                                <el-select v-model="men.gender" placeholder="Select" v-on:change="saveGroup()">
                                    <el-option
                                            v-for="item in options"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="6" v-if="(indexMen != 0)">
                                <el-button v-on:click="removePerson(family.id,indexMen)" type="text" class="remove-button">
                                    <i class="fas fa-minus-circle"></i>
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-button v-on:click="addPerson(family.id)" type="primary" plain>Add person</el-button>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
                <span v-if="(setupScene.length < 5)">
                    <el-divider></el-divider>
                    <el-row>
                        <el-col :span="24">
                            <el-button v-on:click="addFamily" type="primary" plain>Add family</el-button>
                        </el-col>
                    </el-row>
                </span>
                <!--                <el-divider></el-divider>-->
                <!--                <code>{{setupScene}}</code>-->
                <div style="min-height:550px;">&nbsp;</div>
            </el-tab-pane>
            <el-tab-pane label="Play" name="play">
                <Container
                        orientation="horizontal"
                        @drop="onColumnDrop($event)"
                        drag-handle-selector=".column-drag-handle"
                        @drag-start="dragStart"
                        :drop-placeholder="upperDropPlaceholderOptions">
                    <Draggable v-for="(family,index) in scene.families" :key="family.id">
                        <div :class="family.props.className">
                            <div class="card-column-header">
                                <span class="column-drag-handle">&#x2630;</span>
                                {{ family.name }}
                                <div v-if="(index==0)" style="float: right;font-size: 2em;">
                                    <i class="fas fa-home"></i>
                                </div>
                            </div>
                            <br clear="all">
                            <Container
                                    group-name="col"
                                    @drop="(e) => onCardDrop(family.id, e)"
                                    @drag-start="(e) => startDragMen(e)"
                                    @drag-end="(e) => stopDragMen(e)"
                                    :get-child-payload="getCardPayload(family.id)"
                                    drag-class="card-ghost"
                                    drop-class="card-ghost-drop"
                                    :drop-placeholder="dropPlaceholderOptions">
                                <Draggable v-for="(men,indexMen) in family.people" :key="men.id">
                                    <div class="people-wrapper">
                                        <div :id="men.gender">
                                            <div class="arrow-left" :style="'color:'+men.color">
                                                <i class="fas fa-arrow-left" v-show="(men.mood != 'normal')"></i>
                                            </div>
                                            <div class="icon" :style="'color:'+men.color">
                                                <i class="fas" :class="men.gender"></i>
                                            </div>
                                            <div class="arrow-right" :style="'color:'+men.color">
                                                <i class="fas fa-arrow-right" v-show="(men.mood != 'normal')"></i>
                                            </div>
                                        </div>
                                        <br clear="all">
                                        <small :style="'color:'+men.color" style="margin-left:10px;">
                                            <strong>{{men.name}}</strong>
                                        </small>
                                        <span v-if="(indexMen==0)"><el-divider></el-divider></span>
                                    </div>
                                </Draggable>
                            </Container>
                        </div>
                    </Draggable>
                </Container>
                <!--                <el-divider></el-divider>-->
                <!--                <code>{{scene}}</code>-->
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>

    import {Container, Draggable} from 'vue-smooth-dnd'
    import {applyDrag} from '../utils/helpers'
    import VSwatches from 'vue-swatches'
    import Rules from "./rules";

    export default {
        name: 'ruleOfFour',
        components: {Rules, Container, Draggable, VSwatches},
        mounted () {
            const setupScene = JSON.parse(this.$localStorage.get('yoursocialgroup'));
            if (setupScene) {
                this.setupScene = setupScene;
            }
        },
        data() {
            return {
                // fa-male fa-female fa-child fa-baby fa-dog
                options: [{
                    value: 'fa-male',
                    label: 'Male'
                }, {
                    value: 'fa-female',
                    label: 'Female'
                }, {
                    value: 'fa-child',
                    label: 'Child'
                }, {
                    value: 'fa-baby',
                    label: 'Baby'
                }, {
                    value: 'fa-dog',
                    label: 'Dog'
                }, {
                    value: 'fa-cat',
                    label: 'Cat'
                }],
                activeName: 'rules',
                setupScene:[],
                scene,
                upperDropPlaceholderOptions: {
                    className: 'cards-drop-preview',
                    animationDuration: '150',
                    showOnTop: true
                },
                dropPlaceholderOptions: {
                    className: 'drop-preview',
                    animationDuration: '150',
                    showOnTop: true
                }
            }
        },
        methods: {
            makeYourGroup() {
                this.activeName = 'config';
            }
            , addFamily() {
                let id = this.setupScene.length + 1;
                let family = {
                    id: id
                    , name: ''
                    , color: '#000000'
                    , isHome: false
                    , status: 'normal'
                    , type: 'container'
                    , props: {
                        orientation: 'vertical'
                        , className: 'card-container'
                    }
                    , people: [{
                        id: id + '-1'
                        , familyId: id
                        , name: ''
                        , mood: 'normal'
                        , gender: 'fa-male'
                        , color: '#000000'
                        , type: 'draggable'
                        , props: {
                            className: 'card',
                        }
                    }]
                };
                this.setupScene.push(family);
                this.saveGroup();
            }
            , removeFamily(index) {
                this.setupScene.splice(index, 1);
                this.saveGroup();
            }
            , addPerson(familyId) {
                let family = this.findFamily(familyId);
                let men = {
                    id: familyId + '-1'
                    , familyId: familyId
                    , name: ''
                    , gender: 'fa-female'
                    , color: family.color
                    , type: 'draggable'
                    , mood: 'normal'
                    , props: {
                        className: 'card',
                    }
                }
                family.people.push(men);
                this.saveGroup();
            }
            , removePerson(familyId, index) {
                let family = this.findFamily(familyId);
                family.people.splice(index, 1);
                this.saveGroup();
            }
            , changeFamilyColor(familyId) {
                console.log('change family color');
                let family = this.findFamily(familyId);
                console.log(family.color);
                for (let i = 0; i < family.people.length; i++) {
                    family.people[i].color = family.color;
                }
                this.saveGroup();
            }
            , findFamily(familyId) {
                for (let i = 0; i < this.setupScene.length; i++) {
                    if (this.setupScene[i].id == familyId) return this.setupScene[i];
                }
            }
            , saveGroup() {
                this.$localStorage.set('yoursocialgroup', JSON.stringify(this.setupScene))
            }
            , resetPlay(tab) {
                if (tab.name == 'play') {
                    console.log(tab.name);
                    this.scene.families = JSON.parse(JSON.stringify(this.setupScene));
                }
            }
            // ------------------------------------------------------------------------------------- drag and drop stuff
            , onColumnDrop(dropResult) {
                const scene = Object.assign({}, this.scene)
                scene.families = applyDrag(scene.families, dropResult)
                this.scene = scene
            }
            , onCardDrop(columnId, dropResult) {
                if (dropResult.removedIndex !== null || dropResult.addedIndex !== null) {
                    const scene = Object.assign({}, this.scene)
                    const column = scene.families.filter(p => p.id === columnId)[0]
                    const columnIndex = scene.families.indexOf(column)
                    const newColumn = Object.assign({}, column)
                    newColumn.people = applyDrag(newColumn.people, dropResult)
                    scene.families.splice(columnIndex, 1, newColumn)
                    this.scene = scene
                    this.validateScene();
                }
            }
            , getCardPayload(columnId) {
                return index => {
                    return this.scene.families.filter(p => p.id === columnId)[0].people[index]
                }
            }
            , startDragMen(event) {
                if (!(event.payload.gender == 'fa-dog' || event.payload.gender == 'fa-cat')) {
                    event.payload.mood = 'distance';
                }
            }
            , stopDragMen() {
                this.validateScene();
            }
            , dragStart() {
                // console.log('drag started')
            }
            , log() { // ...params
                // console.log(...params)
            }
            // ------------------------------------------------------------------------------------- validate scene
            , validateScene() {
                console.log('validateScene');
            }
        }
    }

    const scene = {
        type: 'container',
        props: {orientation: 'horizontal'},
        families: []
    }

</script>

<style scoped>

    .arrow-left {
        border-top: 1px solid rgba(0, 0, 0, 0);
        display: block;
        float: left;
        width: 33% !important;
        font-size: 3em;
    }

    .arrow-right {
        /*border:1px solid black;*/
        display: block;
        float: left;
        width: 25%;
        text-align: right;
        font-size: 3em;
    }

    .icon {
        /*border:1px solid black;*/
        display: block;
        text-align: center;
        float: left;
        width: 40%;
    }

    #fa-male .arrow-left, #fa-female .arrow-left, #fa-male .arrow-right, #fa-female .arrow-right {
        margin-top: 40px;
    }

    #fa-child .arrow-left, #fa-child .arrow-right {
        margin-top: 25px;
    }

    #fa-male .icon, #fa-female .icon {
        font-size: 7em;
    }

    #fa-child .icon {
        font-size: 5.5em;
    }

    #fa-baby .icon {
        font-size: 4em;
    }

    #fa-cat .icon, #fa-dog .icon {
        font-size: 3em;
    }

    .people-wrapper {
        clear: both;
        display: block;
        width: 100%;
        text-align: center;
    }

    .remove-button {
        color:silver;
    }

    .remove-button:hover {
        color:orangered;
    }

</style>
